.header-div {
  width: 100%;
  height: 35px;
  margin-top: 16px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.header-call {
  position: absolute;
  top: 2px;
  right: 54px;
}

.header-logo {
  width: 45px;
  height: 35px;
  flex-shrink: 0;
  position: absolute;
  top: 0;
  left: 16px;
}

.header-hamburger {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  position: absolute;
  top: 0;
  right: 16px;
}

.svg-header {
  cursor: pointer;
  background-color: #0000;
  border: none;
}

.header-list {
  display: none;
}

.header-icon-logo {
  width: 45px;
  height: 35px;
  flex-shrink: 0;
}

.header-icon-call {
  width: 22px;
  height: 22px;
}

.header-icon-burger {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
}

