.menu-toggle {
  height: 24px;
  width: 24px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.menu-container {
  width: 100%;
  background: var(--pink, #e86f54);
  z-index: 999;
  height: 100%;
  padding: 0;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.header-modal-menu {
  flex-direction: column;
  align-items: center;
  gap: 26px;
  margin-top: 124px;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.header-modal-menu-text {
  color: #fff;
  cursor: pointer;
  margin-top: 0;
  margin-bottom: 0;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.modal-icon-close {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
}

